(function() {
  'use strict';

  angular
    .module('detroitstartupjobs', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ngMaterial', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('HomeCtrl', HomeCtrl);

  /** @ngInject */
  function HomeCtrl($scope) {
    $scope.data = "Hello World"
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/home/home.html',
        controller: 'HomeCtrl',
        controllerAs: 'home'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("detroitstartupjobs").run(["$templateCache", function($templateCache) {$templateCache.put("app/home/home.html","<md-toolbar md-scroll-shrink=\"\"><div class=\"md-toolbar-tools\"><md-button class=\"md-icon-button\" aria-label=\"Settings\"><md-icon md-svg-icon=\"img/icons/menu.svg\"></md-icon></md-button><h2><span>Detroit Startup Jobs</span></h2><span flex=\"\"></span><md-button class=\"md-icon-button\" aria-label=\"Favorite\"><md-icon md-svg-icon=\"img/icons/favorite.svg\" style=\"color: greenyellow;\"></md-icon></md-button><md-button class=\"md-icon-button\" aria-label=\"More\"><md-icon md-svg-icon=\"img/icons/more_vert.svg\"></md-icon></md-button></div></md-toolbar><md-card layout=\"row\"><div flex=\"\" flex-offset=\"33\"><md-list><md-list-item ng-repeat=\"job in jobs\">{{ job.title }}</md-list-item></md-list></div></md-card>");}]);